  
  .project {
    height: 100vh;
    background: rgba(128, 128, 128, 0.03);
  }

  .project, .project-detail {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .project-name {
    font-size: 40px;
    margin-right: 200px;
  }

  .detail {
    display: flex;
  }

  .project-explain, .detail1, .detail2, .detail3, .detail4 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .detail1, .detail2, .detail3, .detail4 {
    margin-right: 30px;
  }

  .explain {
    margin-bottom: 60px;
    font-size: 16px;
    font-weight: 600;
    line-height: 1.4em;
  }

  .detail-name {
    margin-bottom: 10px;
    font-size: 14 px;
    font-weight: 600;
  }

  .detail-explain {
    height: 0;
    font-size: 14px;
  }

  .project-photo {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }

  .notebook {
    width: 1000px;
    height: 1000px;
    position: absolute;
    /* bottom: 140px; */
    object-fit: cover;
  }

  .project_noteboock {
    position: absolute;
    width: 780px;
    height: 600px;
    object-fit: cover;
    overflow: hidden;
    z-index: 2;
  }

  .website {
    height: 120vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: rgba(128, 128, 128, 0.03);
  }

  .website-bar {
    display: flex;
    align-items: center;
    flex-direction: row;
    position: relative;
    border-radius: 20px;
    width: 1020px;
    height: 60px;
    padding-left: 20px;
    padding-bottom: 20px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.24);
  }

  .circle1, .circle2, .circle3 {
    width: 16px;
    height: 16px;
    border-radius: 100%;
    background: gray;
    margin: 8px;
  }

  .project-video{
    opacity: 0.9;
    width: 1040px;
    position: relative;
    bottom: 20px;
    padding: 0;
    height: 600px;
    object-fit: cover;
    box-sizing: border-box;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.24);
  }

  .etc1, .etc3 {
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .etc2 {
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(128, 128, 128, 0.03);
  }

  .etc-title {
    width: 160px;
  }

  .etc-title h1 {
    font-size: 30px;
    text-align: center;
  }

  .etc-explain {
    margin-left: 100px;
    font-size: 16px;
    font-weight: 600;
    width: 740px;
  }