
  .profile-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .myname {
    font-family: 'Raleway', sans-serif;
  }

  .contract, .contract:active, .contract:hover, .contract:checked, .contract:visited {
    color: blanchedalmond;
    text-decoration: none;
  }
  
  .visually-hidden {
    clip: rect(0 0 0 0);
    -webkit-clip-path: inset(50%);
            clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
  }
  
  .flex {
    align-items: center;
    display: flex;
    justify-content: center;
  }
  
  .scene .profile-photo {
    background: rgba(255, 255, 255, 0.75) center/cover;
    background-size: 100% 100%;
    width: 100vw;
    height: 100vh;
    position: relative;
  }
  
  .card {
    -webkit-backdrop-filter: blur(20px);
            backdrop-filter: blur(20px);
    background: rgba(254, 254, 254, 0.075);
    border: 1px solid rgba(254, 254, 254, 0.18);
    border-radius: 22px;
    box-shadow: 1px 1px 50px rgba(191, 183, 183, 0.75);
    color: rgba(255, 255, 255, 0.93);
    max-width: 300px;
    overflow: hidden;
    padding: 50px 40px 50px 40px;
    position: absolute;
    text-align: center;
    z-index: 1;
    width: 40vw;
    height: 50vh;
  }

  .card-figure {
    height: auto;
    margin: 0 auto;
    max-width: 225px;
    width: 60%;
  }
  
  .card-image {
    border-radius: 50%;
    max-width:100%;
    width: 180px;
    height: 180px;
  }
  
  .card-heading {
    font-size: 1.6rem;
    font-weight: 600;
    text-shadow: 1px 1px 10px white;
    margin-bottom: 15px;
    color: rgb(83, 78, 78);
  }

  #profile1 {
    opacity: 0;
    animation: fadeIn 1s ease-in-out 0.02s forwards;
  }

  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  