
    .textCover {
        overflow: hidden;
        transform: rotate(90deg);
        position: relative;
        top: 300px;
        right: 460px;
    }

    .imgCover1 {
        overflow: hidden;
        position: relative;
        top: 240px;
        left: 160px;
    }

    .imgCover2 {
        overflow: hidden;
        position: relative;
        right: 240px;
        bottom: 260px;  
    }

    .more {
        position: relative;
        display: flex;
        bottom: 540px;
        left: 430px;
        cursor: pointer;
        z-index: 1;
    }

    .bi-arrow-down-right {
        transform: rotate(-45deg);
    }

    .text, .text2, .text3, .text4 {
        font-weight: 600;
        opacity: 0;
    }

    .projectImg, .projectImg2, .projectImg3, .projectImg4 {
        opacity: 0;
        width: 700px;
        height: 400px;
    }

    .projectImg-1 {
        opacity: 0;
        width: 400px;
        height: 280px;
    }

    .text.active, .text2.active, .text3.active, .text4.active {
        animation: show_txt .8s .2s ease-in-out forwards;
    }

    .projectImg.active, .projectImg2.active, .projectImg3.active, .projectImg4.active {
        animation: show_img2 .8s .2s ease-in-out forwards;
    }

    .projectImg-1.active {
        animation: show_img1 .8s .2s ease-in-out forwards;
    }

    @keyframes show_txt {
        0%{
        transform: translateX(-100%);
        opacity: 0;
        }
        100% {
        transform: translateX(0);
        opacity: 1;
        }
    }

    @keyframes show_img1 {
        0%{
            transform: translateX(-100%);
            opacity: 0;
        }
        100% {
            transform: translateX(0);
            opacity: 1;
        }
    }

    @keyframes show_img2 {
        0% {
        clip-path: inset(50% 50% 50% 50%);
        opacity: 0;
        }
        100% {
        clip-path: inset(0 0 0 0);
        opacity: 1;
        }
    }
